/**
 * Load more - show additional items on 'Load more' button click.
 * Update URL to illustrate where user is in item set.
 *
 * @module patterns/load-more/load-more
 * @author Mark Skinsley <mark.skinsley@city.ac.uk>
 * @copyright City, University of London 2019
 */

import { numberFromString, createElement } from '../../util';
import scroll from 'zenscroll';

const className = 'load-more';
let hashedUrl = window.location.hash,
    loadMoreId;
let dskItems = [];
let dskItemsLength = dskItems.length;
let abc;

/**
 * Slider: Enable/disable navigation buttons based on position of listing in collection
 *
 * @param {number} [counter] - Position in dataset.
 * @param {HTMLElement} prevBtn - Previous item button.
 * @param {HTMLElement} nextBtn - Next item button.
 * @param {number} itemsLength - Number of items.
 */
function navBtnState(counter, prevBtn, nextBtn, itemsLength) {
    if (counter === 0) {
        prevBtn.setAttribute('disabled', true);
        nextBtn.removeAttribute('disabled');
    } else if (counter > 0 && counter < itemsLength - 1) {
        prevBtn.removeAttribute('disabled');
        nextBtn.removeAttribute('disabled');
    } else {
        nextBtn.setAttribute('disabled', true);
        prevBtn.removeAttribute('disabled');
    }
}

/**
 * Slider: Show listing entry based on slider button clicks
 *
 * @param {array} [itemsArray] - Items in dataset.
 * @param {number} counter - Position in dataset.
 * @param {number} browserWidth - Browser window width.
 */
function listingDisplay(itemsArray, counter, browserWidth) {
    for (const item of itemsArray.entries()) {
        if (browserWidth < 768 && itemsArray.length > 1) {
            item[0] <= counter
                ? item[1].classList.remove('hide')
                : item[1].classList.add('hide');
        }
    }
}

/**
 * Load more: Control what items will display based on wrapper's 'items-visible' data attribute.
 *
 * @param {HTMLElements} [items] - Elements controlled by 'Load more' button.
 * @param {number} visibleItems - Number of items visible at any one time.
 * @param {HTMLElement} loadMoreBtn - Button controlling particular item group.
 * @param {string} folderId - Folder ID where assets exist.
 */
function itemsDisplay(items, visibleItems, loadMoreBtn) {
    for (const item of items.entries()) {
        item[1].setAttribute('tabindex', '-1');

        if (item[0] >= visibleItems) {
            item[1].classList.add('hide');
        } else {
            item[1].classList.remove('hide');
        }
    }
    // Hide 'load more' button when reached end of listings
    if (visibleItems >= items.length) {
        loadMoreBtn.classList.add('hide');
    } else {
        loadMoreBtn.classList.remove('hide');
    }
}

/**
 * Load more: Scroll to, and focus first listing of newly visible items batch.
 *
 * @param {HTMLElement} [items] - Elements controlled by 'Load more' button.
 * @param {number} visibleItems - Number of items visible at any one time.
 * @param {number} itemsIncrement - Number of additional items shown when 'Load more' button is clicked.
 */
function scrollToItem(items, a_visibleItems, a_itemsIncrement) {
    let targetItem = a_visibleItems - a_itemsIncrement;
    for (const item of items.entries()) {
        if (item[0] == targetItem) {
            scroll.to(item[1]);
            item[1].focus();
        } else if (a_visibleItems == a_itemsIncrement) {
            if (item[0] == 0) {
                item[1].focus();
                scroll.to(item[1]);
            }
        }
    }
}

/**
 * Load more: Push state to URL: used to build initial hash after first 'Load more' click.
 *
 * @param {string} folderId - Folder ID where assets exist.
 * @param {number} visibleItems - Number of items visible at any one time.
 * @param {number} itemsIncrement - Number of additional tems shown when 'Load more' button is clicked.
 */
function pushUrlState(folderId, a_visibleItems, a_itemsIncrement) {
    // console.log('vis ' + a_visibleItems);
    // console.log('inc ' + a_itemsIncrement);
    let targetListingUrlParam = a_visibleItems - (a_itemsIncrement - 1);
    history.pushState(
        '',
        '',
        `#folder${folderId}-item${targetListingUrlParam}`
    );
}

/**
 * Load more: Replace URL state: used to swap existing hash
 *
 * @param {HTMLElement} folderId - Folder ID where assets exist.
 * @param {number} visibleItems - Number of items visible at any one time.
 * @param {number} itemsIncrement - Number of additional tems shown when 'Load more' button is clicked.
 */
function replaceUrlState(folderId, a_visibleItems, a_itemsIncrement) {
    let targetListingUrlParam = a_visibleItems - (a_itemsIncrement - 1);
    history.replaceState(
        '',
        '',
        `#folder${folderId}-item${targetListingUrlParam}`
    );
}

function launchLoadMore(e) {
    let folderId = e.getAttribute('id'),
        items = e.querySelectorAll('.item'),
        itemsArray = Array.from(items),
        itemsLength = Array.from(items).length,
        itemsIncrement = parseInt(e.dataset.increment),
        defaultVisibleItems = parseInt(e.dataset.itemsVisible),
        active = e.setAttribute('active', false),
        makeSlider = e.getAttribute('data-slider'),
        contentContainer = e.querySelector('.items-group'),
        browserWidth = document.documentElement.scrollWidth,
        counter = 0,
        digit = e.querySelector('.content-slider__position__digit');
    //digitText = digit.innerText;
    // digitTextParts = digitText.split(" / ");
    // console.log(digitTextParts);

    // Create & append 'load more' button
    const loadMoreBtn = document.createElement('button');
    loadMoreBtn.classList.add('load-more-btn');

    let loadMorePlusIcon = document.createElement('span');
    loadMorePlusIcon.classList.add('far', 'fa-plus-circle', 'icon-plus-circle');
    loadMorePlusIcon.setAttribute('aria-hidden', true);

    let loadMorePlusLabel = document.createElement('span');
    loadMorePlusLabel.classList.add('load-more-label');
    let loadMorePlusLabelText = document.createTextNode('Load more');
    loadMorePlusLabel.appendChild(loadMorePlusLabelText);

    const sliderControls = e.querySelector('.content-slider');

    const sliderBtnPrev = createElement(
        'button',
        null,
        'Previous',
        null,
        'fas',
        'fa-arrow-left',
        'arrow-left--btn-prev'
    );
    const sliderBtnNext = createElement(
        'button',
        null,
        'Next',
        null,
        'fas',
        'fa-arrow-right',
        'arrow-right--btn-next'
    );
    let sliderPosition = createElement(
        'span',
        `${counter + 1} / ${itemsLength}`,
        'Slider position',
        null,
        'content-slider__position__digit'
    );

    // Disable slider previous button on load
    navBtnState(counter, sliderBtnPrev, sliderBtnNext, itemsLength);
    listingDisplay(itemsArray, counter, browserWidth, makeSlider);
    let abcArr = [];
    let counterEl;
    let counterElText;

    sliderBtnPrev.addEventListener('click', () => {
        // sliderControls.style.display = 'block';

        counterEl = document.createElement('p');
        counterEl.classList.add('content-slider__position__digits');
        counterEl.classList.add('counter');
        counterElText = document.createTextNode(`${counter} / ${itemsLength}`);
        counterEl.appendChild(counterElText);
        sliderControls.appendChild(counterEl);
        // contentContainer.removeChild(items);

        counter -= 1;
        // console.log(digit);
        abc = counter;
        abcArr.push(abc);
        e.setAttribute('data-items-visible', abc + 1);
        navBtnState(counter, sliderBtnPrev, sliderBtnNext, itemsLength);
        listingDisplay(itemsArray, counter, browserWidth, makeSlider);
        for (const item of items.entries()) {
            if (item[0] > counter) {
                item[1].classList.add('dsk');
                item[1].classList.add('hide');
            } else if (item[0] == counter) {
                item[1].classList.remove('dsk');
            }
        }
        let loadMoreClicks = 0;

        // Pass counter value, i.e. position in listing to functions
        loadMoreBtn.addEventListener('click', () => {
            // // console.log('abc: ' + abcArr[abcArr.length-1]);

            loadMoreClicks += 1;
            abc =
                abcArr[abcArr.length - 1] + 1 + loadMoreClicks * itemsIncrement;

            // // console.log(loadMoreClicks);

            e.setAttribute('data-items-visible', abc);

            let currentlyVisibleItems = parseInt(e.dataset.itemsVisible);
            // // console.log(currentlyVisibleItems)
            itemsDisplay(items, currentlyVisibleItems, loadMoreBtn);
            scrollToItem(items, abc, itemsIncrement);
            hashedUrl
                ? replaceUrlState(folderId, abc, itemsIncrement)
                : pushUrlState(folderId, abc, itemsIncrement);
        });
    });

    sliderBtnNext.addEventListener('click', () => {
        // console.log(counter);
        // sliderControls.style.display = 'block';
        counter += 1;

        counterEl = document.createElement('p');
        counterEl.classList.add('content-slider__position__digits');
        counterEl.classList.add('counter');
        counterElText = document.createTextNode(
            `${counter + 1} / ${itemsLength}`
        );
        counterEl.appendChild(counterElText);
        sliderControls.appendChild(counterEl);

        // // console.log('counter to be passed: ' + counter);
        // Create new variable to override counter being set on any load more click
        abc = counter;
        digit.innerText = 'none';
        navBtnState(counter, sliderBtnPrev, sliderBtnNext, itemsLength);
        listingDisplay(itemsArray, counter, browserWidth, makeSlider);
        for (const item of items.entries()) {
            if (item[0] < counter) {
                item[1].classList.add('dsk');
                dskItems.push(item[1]);
            } else if (item[0] > counter) {
                item[1].classList.add('hide');
            } else if (item[0] == counter) {
                // // console.log('equal');
                item[1].classList.remove('dsk');
                item[1].classList.remove('hide');
            }
        }
        // Pass counter value, i.e. position in listing to functions
        loadMoreBtn.addEventListener('click', () => {
            // console.log('counter on load more click: ' + abc);
            itemsDisplay(items, abc + itemsIncrement + 1, loadMoreBtn);
            scrollToItem(items, abc + itemsIncrement + 1, itemsIncrement);
            hashedUrl
                ? replaceUrlState(
                      folderId,
                      abc + itemsIncrement + 1,
                      itemsIncrement
                  )
                : pushUrlState(
                      folderId,
                      abc + itemsIncrement + 1,
                      itemsIncrement
                  );
        });
    });

    /**
     * Only add load more button to the DOM if more than one item exists and 'data-items-visible'
     * is less than number of items in listing group.
     **/

    if (items.length > 1 && defaultVisibleItems < items.length) {
        loadMoreBtn.appendChild(loadMorePlusIcon);
        loadMoreBtn.appendChild(loadMorePlusLabel);
        contentContainer.appendChild(loadMoreBtn);
    }

    /**
     * Give wrapper a numeric data attribute. As this changes, so
     * will the number of visible items.
     */
    let visibleItems = parseInt(e.dataset.itemsVisible);

    // Load correct number of items based on URL hash
    if (hashedUrl) {
        let hashedUrlParts = hashedUrl.split('-');
        let activeItem = parseInt(numberFromString(hashedUrlParts[1]));
        let activeFolderId = parseInt(numberFromString(hashedUrlParts[0]));
        let activeFolder = document.getElementById(activeFolderId);
        let activeFolderItems = activeFolder.querySelectorAll('.item');
        let activeFolderItemIncrement = parseInt(
            activeFolder.dataset.increment
        );
        visibleItems = activeItem + (itemsIncrement - 1);

        /**
         * Global variable that identifies which load more instance is being referenced
         * in the URL. This is necessary to control how this, and not currently active
         * load more instances should behave.
         */
        loadMoreId = activeFolderId;

        let activeFolderVisibleItems =
            activeItem + (activeFolderItemIncrement - 1);

        // Hide 'load more' button when reached end of listings
        if (visibleItems <= items.length) {
            loadMoreBtn.classList.add('hide');
        } else {
            loadMoreBtn.classList.remove('hide');
        }

        // When hashed page loads, ensure non-active load more listings are isolated.
        if (folderId != activeFolderId) {
            let notActiveFolderId = parseInt(folderId);
            let notActiveFolder = document.getElementById(notActiveFolderId);
            let notActiveFolderItems = notActiveFolder.querySelectorAll(
                '.item'
            );
            let notActiveVisibleItems = parseInt(
                notActiveFolder.dataset.itemsVisible
            );
            let notActiveFolderBtn = notActiveFolder.querySelector(
                '.load-more-btn'
            );
            itemsDisplay(
                notActiveFolderItems,
                notActiveVisibleItems,
                notActiveFolderBtn,
                notActiveFolderId
            );
        } else {
            // Look for active folder variables
            itemsDisplay(
                activeFolderItems,
                activeFolderVisibleItems,
                loadMoreBtn,
                folderId
            );
            scrollToItem(
                activeFolderItems,
                activeFolderVisibleItems,
                activeFolderItemIncrement
            );
            activeFolder.setAttribute('active', true);
        }
    } else {
        // Plain URL (no hash) => load default view for all load more instances
        itemsDisplay(items, visibleItems, loadMoreBtn);
    }

    // If slider, hide 'load more' button and show slider controls
    if (makeSlider == 'true') {
        contentContainer.classList.add('slider-compatible');
        contentContainer.appendChild(sliderBtnPrev);
        contentContainer.appendChild(sliderBtnNext);
        contentContainer.appendChild(sliderPosition);
        if (browserWidth < 768) {
            for (const item of items.entries()) {
                if (!item[1].classList.contains('hide') && item[0] > 0) {
                    item[1].classList.add('hide');
                }
            }
            // sliderControls.style.display = 'block';
        } else {
            // sliderControls.style.display = 'none';
        }
    }

    // Run on every 'load more' click: increase listings by batch number
    loadMoreBtn.addEventListener('click', () => {
        active = e.getAttribute('active');
        sliderBtnPrev.removeAttribute('disabled');

        if (makeSlider) {
            for (const item of items.entries()) {
                if (item[0] == visibleItems) {
                    item[1].classList.remove('dsk');
                } else {
                    item[1].classList.add('dsk');
                }
                counter = visibleItems;
            }
        }

        if (dskItemsLength > 0) {
            // // console.log(dskItemsLength);
            itemsDisplay(items, dskItemsLength, loadMoreBtn);
            scrollToItem(items, 7, 1);

            if (hashedUrl) {
                // // console.log('hashed');
                replaceUrlState(
                    folderId,
                    visibleItems + itemsIncrement,
                    itemsIncrement
                );
            } else {
                // // console.log('no hash');
                pushUrlState(
                    folderId,
                    dskItemsLength + itemsIncrement,
                    itemsIncrement
                );
            }

            // hashedUrl
            //     ? replaceUrlState(folderId, dskItemsLength, itemsIncrement)
            //     : pushUrlState(folderId, dskItemsLength, itemsIncrement);

            for (const item of items.entries()) {
                // if (item[0] < (dskItemsLength + itemsIncrement - 1)) {
                item[1].classList.remove('hide');
                // }
                // // console.log(dskItemsLength);
            }
        }

        /**
         * Check if load more is already active, i.e. already had at least one
         * click on it. If it has, track the number
         * of visible items. If it is not active, i.e. first click, retrieve data attribute settings from launch and
         * use these to decide how many items should be shown.
         */
        if (active == 'true') {
            visibleItems += itemsIncrement;
        } else {
            visibleItems = defaultVisibleItems + itemsIncrement;
        }

        e.setAttribute('data-items-visible', visibleItems);
        e.setAttribute('active', true);

        itemsDisplay(items, visibleItems, loadMoreBtn);
        scrollToItem(items, visibleItems, itemsIncrement);
        hashedUrl = window.location.hash;
        hashedUrl
            ? replaceUrlState(folderId, visibleItems, itemsIncrement)
            : pushUrlState(folderId, visibleItems, itemsIncrement);
        loadMoreId = e.id;
    });

    // Back/forward browser clicks.
    window.onpopstate = () => {
        let loadMoreIdElement = document.getElementById(loadMoreId);

        // Must re-assign variable on pop state change
        hashedUrl = window.location.hash;
        let relatedItems = loadMoreIdElement.querySelectorAll('.item');
        relatedItems = Array.from(relatedItems);
        visibleItems = itemsIncrement;

        if (hashedUrl) {
            /**
             * From non-hashed URL to hashed URL. This could be a forward browser click, e.g.
             * '/' to '#folder479366-item4'. Target correct load more group (#folder479366)
             * and active item within this group (item4).
             */
            let updatedUrlParts = window.location.hash.split('-');
            let currentItem = parseInt(numberFromString(updatedUrlParts[1]));
            currentItem = currentItem + (itemsIncrement - 1);
            visibleItems = currentItem;
            e.setAttribute('data-items-visible', visibleItems);
            itemsDisplay(items, visibleItems, loadMoreBtn, folderId);
            scrollToItem(relatedItems, visibleItems, itemsIncrement);
        } else {
            /**
             * From hashed URL to non-hashed URL. With hash removed, go to to
             * first item in originally hashed load more item group.
             */
            scrollToItem(relatedItems, visibleItems, itemsIncrement);
            visibleItems = visibleItems + itemsIncrement;
        }
    };
}

export default {
    launchFn: launchLoadMore,
    launchQuery: `.${className}`,
};
